body {
  display: flex;
  justify-content: center;
}

.main {
  display: flex;
  gap: 20px;
  flex-direction: column;
}

.app {
  padding: 15px;
  background-color: rgb(216, 216, 216);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 10px;
  width: 500px;
  border-radius: 13px;
}

.button {
  background-color: #4CAF50;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  width: 200px;
  transition: 0.5s;
}

.button:hover {
  background-color: #87b289;
}

.input {
  background-color: #ffffff;
  border: 1px solid black;
  border-radius: 10px;
  padding: 0 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  width: 200px;
}

.a {
  display: none;
}